import React from 'react';
import { renderHook, act } from '@testing-library/react-native';
import { Animated } from 'react-native';
import useModalAnimation from '../../../../src/components/ui/Modal/useModalAnimation';

// On espionne les fonctions clés de la librairie Animated
const mockStart = jest.fn();
jest.spyOn(Animated, 'timing').mockImplementation(() => ({
  start: mockStart,
  stop: jest.fn(),
}));

jest.spyOn(Animated, 'Value').mockImplementation(() => ({
  interpolate: jest.fn(),
  addListener: jest.fn(),
  removeAllListeners: jest.fn(),
}));

describe('useModalAnimation', () => {
  // Réinitialise les mocks avant chaque test
  beforeEach(() => {
    jest.clearAllMocks();
  });

  it('ne devrait pas démarrer d\'animation si isVisible est faux à l\'initialisation', () => {
    renderHook(() => useModalAnimation({ isVisible: false }));
    expect(Animated.timing).not.toHaveBeenCalled();
    expect(mockStart).not.toHaveBeenCalled();
  });

  it('devrait démarrer l\'animation si isVisible devient vrai', () => {
    const { rerender } = renderHook(({ isVisible }) => useModalAnimation({ isVisible }), {
      initialProps: { isVisible: false },
    });

    rerender({ isVisible: true });

    expect(Animated.timing).toHaveBeenCalledTimes(1);
    expect(mockStart).toHaveBeenCalledTimes(1);
  });

  it('devrait appeler Animated.timing avec les bonnes valeurs pour la position "top"', () => {
    const { rerender } = renderHook(({ isVisible, position }) => useModalAnimation({ isVisible, position }), {
      initialProps: { isVisible: false, position: 'top' },
    });

    rerender({ isVisible: true, position: 'top' });

    expect(Animated.timing).toHaveBeenCalledWith(
      expect.anything(),
      expect.objectContaining({
        toValue: 1,
        duration: 300,
      })
    );
  });

  it('devrait retourner les styles de transformation corrects pour la position "top"', () => {
    const { result } = renderHook(() => useModalAnimation({ isVisible: true, position: 'top' }));
    expect(result.current.transformStyle).toEqual(
      expect.objectContaining({
        transform: expect.arrayContaining([
          expect.objectContaining({
            translateY: expect.any(Object),
          }),
        ]),
      })
    );
  });
});